import React from "react";
import TechCard from "./TechCard";
import Git from "../images/git.svg";
import VScode from "../images/visualstudiocode.svg";
import Netlify from "../images/netlify.svg";
import Firebase from "../images/firebase.svg";

const ToolStack = () => {
  return (
    <div className="container-xl my-5 d-flex row div-divider">
      <div className="jumbotron py-3">
        <h1>My Tool Stack</h1>
      </div>
      <TechCard src={Git} alt="./git" />
      <TechCard src={Netlify} alt="Netlify" />
      <TechCard src={Firebase} alt="Firebase" />
      <TechCard src={VScode} alt="Visual Studio Code" />
    </div>
  );
};

export default ToolStack;
